body {
    font: 14px/2em "PingFang SC", "微软雅黑", "Microsoft YaHei", Helvetica, "Helvetica Neue", Tahoma, Arial, sans-serif;
    color: #2b333b;
    background: #f7f7f7;
}

html,
body {
    height: 100%;
}

html {
    font-size: 50px;
}

* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    -webkit-font-smoothing: antialiased;
}

.fl {
    float: left;
}

.fr {
    float: right;
}
.th{
    font-weight: bold;
    font-size: 1.2em;
}
.text-center{
    text-align: center;
}
.big {
    font-size: 1.1em;
}

.grey {
    color: #888
}

.text-l {
    text-align: left;
}

.text-r {
    text-align: right;
}

.clear {
    clear: both;
    height: 0 !important;
    padding: 0 !important;
    margin: 0 !important;
}

.menu-hide {
    display: none;
}

.menu-active {
    display: block;
}

.fixed-top {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 10;
}

.fixed-bottom {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 10;
}

.app-content {
    padding: 55px 0;
}
.Header {
    border-bottom: 1px solid #f0f0f0;
}
.swipe-num div {
    display: inline-block;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background: rgba(255, 255, 255, .3);
    margin: 0 5px;
}

.swipe-num {
    position: relative;
    text-align: center;
    top: -30px;
    z-index: 20;
    height: 0;
}

.swipe-num div.active {
    background: #fff;
}

