.Course {
    padding: 40px 0 10px;
}
.course{
    padding: 10px 0;
}
.Course .course{
    padding: 0 0 10px;
}

.course-content {
    width: 100%;
}

.course-content>div {
    color: rgba(255, 255, 255, .8);
    width: 100%;
    margin: 0 0 8px;
    border-bottom: 1px solid #f0f0f0;
}
.paddLR{
    padding: 0 15px;
}
.paddT{
    padding-top: 10px; 
    background: #fff;
}
.paddTB{
    padding-top: 20px;
    padding-bottom: 20px; 
}
.course-content>div:hover {
    background: #5e5e5e47;
}
.course-content.half{
    padding: 0 3%;
}
.course-content.half>div {
    width: 48%;
    padding: 5px 10px;
    float: left;
    margin: 10px 0 15px;
    border-radius: 10px;
}

.course-content.half>div:nth-child(2n) {
    margin-left: 2%;
}

.course-content.half>div:nth-child(2n+1) {
    margin-right: 2%;
}

.course-content img {
    display: block;
    min-height: 75px;
}

.lineH-low { 
    line-height: 1.4;
    display: block;
    color: #6e6a6a;
}

