.Course .am-flexbox.am-flexbox-align-content-center{
    position: fixed;
    width: 100%;
    top: 55px;
    left: 0;
}


.am-menu .am-flexbox .am-flexbox-item:first-child .am-list .am-list-item .am-list-line .am-list-content {
    text-align: center;
}
.am-menu .am-flexbox .am-flexbox-item .am-list .am-list-item .am-list-line .am-list-content{
    font-size: 14px;
    padding: 0!important;
}
.am-list-item{
    padding-left: 0!important;
}
.menu-tip {
    text-align: center;
    background: #eee;
    border-bottom: 1px solid #ddd;
}

.menu-tip .am-flexbox-item {
    line-height: 40px;
    position: relative;
}

.menu-tip .am-flexbox-item i {
    position: absolute;
    top: 16px;
    right: 10px;
    width: 5px;
    height: 5px;
    border: solid 1px #787373;
    border-width: 0 1px 1px 0;
    -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
            transform: rotate(45deg);
}

.menu-tip .am-flexbox-item i.rotate {
    -webkit-transform: rotate(225deg);
        -ms-transform: rotate(225deg);
            transform: rotate(225deg);
}

.am-list .am-list-item.am-radio-item .am-list-line .am-list-extra .am-radio-inner {
    display: none;
}

.Course .am-menu {
    position: fixed;
    width: 100%;
    top: 95px;
    left: 0;
    -webkit-box-shadow: 4px 4px 10px #eee;
            box-shadow: 4px 4px 10px #eee;
}

.am-toast-notice-content .am-toast-text {
    min-width: 100px;
}

/* detail */

.detail-header {
    width: 100%;
    background: #ddd no-repeat;
    color: #fff;
    padding: 20px;
    position: relative;
    min-height: 210px;
}

.detail-cover {
    position: absolute;
    background: rgba(0, 0, 0, .4);
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    overflow: hidden;
}

.detail-msg {
    position: relative;
    z-index: 5;
}
.detail-header .cover{
    background: rgba(0, 0, 0, .4);
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 0;
    top: 0;
    left: 0;
}

/* search */

.search-box {
    width: 100vw;
    height: 100vh;
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;
    background: #fff;
}

li {
    list-style: none
}

h5 {
