}

/* Find */

.more_list {
    background: #fff;
    margin: 15px 0;
    width: 100%;
    padding-left: 0%;
}

.more_list li {
    height: 50px;
    border-bottom: 1px solid #f0f0f0;
    line-height: 50px;
    font-size: 16px;
    position: relative;
}

.more_list li * {
    vertical-align: middle;
    margin: 0 10px 0 0;
}

.more_list li {
    display: block;
    width: 100%;
    line-height: 50px;
    overflow: hidden;
}

.more_list li img {
    float: left;
    width: 25px;
    height: 25px;
    margin-top: 13px;
    margin-left: 20px;
}

