    float: left;
    font-size: 16px;
    color: #333;
    margin-left: 15px;
}

.more_list li .icon-youjiantou {
    color: #888;
    font-size: 18px;
    position: absolute;
    top: 16px;
    right: 10px;
    width: 10px;
    height: 10px;
    border: solid 1px #787373;
    border-width: 0 1px 1px 0;
    -webkit-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

/* login */

.loginbox {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    max-width: 640px;
    padding: 15% 30px 0;
}

.login_logo {
    margin-bottom: 20px;
}

.login_logo img {
    width: 180px;
    margin: 0 auto;
}

.login-hd {
    margin: 20px 0;
    text-align: center;
}

.login-hd li {
    margin: 0 5px;
    font-size: 20px;
    color: #999;
    line-height: 40px;
    display: inline-block;
}

.login-hd a {
    color: #999;
}

.login-hd .on a {
    color: #333;
}

.login-bd {
    font-size: 14px;
    border-top: solid 1px #e6e6e6;
}

.login-bd .item {
    position: relative;
    height: 50px;
    line-height: 50px;
    border: solid 1px #e6e6e6;
    border-top: 0;
    margin-top: 10px;
}

.login-bd .item:last-child {
    border: 0;
}

.login-bd .item input {
    width: 100%;
    height: 100%;
    padding: 0 10px;
    color: #333;
    background-color: #fff;
    border: none;
}


.login-bd .item.code .num {
    position: absolute;
    left: 0;
    top: 0;
    width: 80px;
    height: 49px;
    border-right: solid 1px #e6e6e6;
    overflow: hidden;
}

.login-bd .item select {
    position: relative;
    width: 100%;
    height: 100%;
    padding: 0 10px;
    color: #333;
    background-color: #fff;
    text-align: center;
}

.login-bd .item.code .num:after {
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    top: 22px;
    right: 15px;
    border: solid 8px #ccc;
    border-color: #ccc transparent transparent;
}

.login-bd .item .yzm,
.login-bd .item .width97 {
    position: absolute;
    right: 10px;
    top: 0;
    padding: 0 10px;
    font-size: 14px;
    color: #00BED4;
}

